/* inconsolata-regular - latin */
@font-face {
  font-family: 'Inconsolata';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url('./fonts/inconsolata-v16-latin-regular.eot'); /* IE9 Compat Modes */
  src: local('Inconsolata Regular'), local('Inconsolata-Regular'),
    url('./fonts/inconsolata-v16-latin-regular.eot?#iefix')
      format('embedded-opentype'),
    /* IE6-IE8 */ url('./fonts/inconsolata-v16-latin-regular.woff2')
      format('woff2'),
    /* Super Modern Browsers */
      url('./fonts/inconsolata-v16-latin-regular.woff') format('woff'),
    /* Modern Browsers */ url('./fonts/inconsolata-v16-latin-regular.ttf')
      format('truetype'),
    /* Safari, Android, iOS */
      url('./fonts/inconsolata-v16-latin-regular.svg#Inconsolata') format('svg'); /* Legacy iOS */
}
@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url('./fonts/roboto-v18-latin-300.eot'); /* IE9 Compat Modes */
  src: local('Roboto Light'), local('Roboto-Light'),
    url('./fonts/roboto-v18-latin-300.eot?#iefix') format('embedded-opentype'),
    /* IE6-IE8 */ url('./fonts/roboto-v18-latin-300.woff2') format('woff2'),
    /* Super Modern Browsers */ url('./fonts/roboto-v18-latin-300.woff')
      format('woff'),
    /* Modern Browsers */ url('./fonts/roboto-v18-latin-300.ttf')
      format('truetype'),
    /* Safari, Android, iOS */ url('./fonts/roboto-v18-latin-300.svg#Roboto')
      format('svg'); /* Legacy iOS */
}
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}



